<template>
	<div class="vue-flux" ref="container" @mouseover="toggleMouseOver(true)" @mouseleave="toggleMouseOver(false)">
		<img v-for="(src, index) in preload" :key="index" :src="path + src" alt="" @load="addImage(index)" @error="addImage(index)" ref="images">

		<div class="mask" :style="sizePx" ref="mask" @touchstart="dragging" @touchend="releasing">
			<component v-if="transition.current" :is="transition.current" ref="transition" :slider="slider" :direction="direction"></component>
			<flux-image :slider="slider" :index="image1Index" ref="image1"></flux-image>
			<flux-image :slider="slider" :index="image2Index" ref="image2"></flux-image>
		</div>

		<slot name="spinner">
			<div v-if="!loaded" class="spinner">
				<div class="pct">{{ this.loadPct }}%</div>
				<div class="border"></div>
			</div>
		</slot>

		<slot name="caption"></slot>

		<slot name="controls"></slot>

		<slot name="index"></slot>

		<slot name="pagination"></slot>
	</div>
</template>

<script>
	import FluxImage from './FluxImage.vue';

	export default {
		name: 'VueFlux',

		components: {
			FluxImage
		},

		data: () => ({
			config: {
				autoplay: false,
				fullscreen: false,
				delay: 5000,
				width: '100%',
				height: 'auto'
			},
			size: {
				width: undefined,
				height: undefined,
			},
			timer: undefined,
			transitionNames: [],
			transition: {
				current: undefined,
				last: undefined
			},
			mouseOver: false,
			dragStartX: undefined,
			dragStartY: undefined,
			image1Index: 0,
			image2Index: 1,
			imagesLoaded: 0,
			loaded: false,
			preload: [],
			properties: []
		}),

		props: {
			options: {
				type: Object,
				default: () => {}
			},
			transitions: {
				type: Object,
				required: true
			},
			path: {
				type: String,
				default: ''
			},
			images: {
				type: Array,
				default: () => []
			},
			captions: {
				type: Array,
				default: () => []
			}
		},

		computed: {
			slider: function() {
				return this;
			},

			touchable: function() {
				return 'touchstart' in document.documentElement;
			},

			caption: function() {
				if (this.$slots['caption'])
					return this.$slots['caption'][0].componentInstance;

				return undefined;
			},

			controls: function() {
				if (this.$slots['controls'])
					return this.$slots['controls'][0].componentInstance;

				return undefined;
			},

			index: function() {
				if (this.$slots['index'])
					return this.$slots['index'][0].componentInstance;

				return undefined;
			},

			pagination: function() {
				if (this.$slots['pagination'])
					return this.$slots['pagination'][0].componentInstance;

				return undefined;
			},

			mask: function() {
				return this.$refs.mask;
			},

			sizePx: function() {
				if (typeof this.size.width !== 'number' || typeof this.size.height !== 'number')
					return {};

				return {
					width: this.size.width +'px',
					height: this.size.height +'px'
				};
			},

			loadPct: function() {
				return Math.ceil(this.imagesLoaded * 100 / this.images.slice(0).length);
			},

			currentImage: function() {
				if (this.$refs.image1 === undefined)
					return undefined;

				return this.$refs.image1.style.zIndex === 11? this.$refs.image1 : this.$refs.image2;
			},

			nextImage: function() {
				return this.$refs.image1.style.zIndex === 10? this.$refs.image1 : this.$refs.image2;
			},

			nextTransition: function() {
				if (!this.transitionNames.length)
					return undefined;

				let nextIndex = this.transition.last + 1;

				if (nextIndex >= this.transitionNames.length)
					nextIndex = 0;

				return this.transitionNames[nextIndex];
			},

			direction: function() {
				return this.currentImage.index < this.nextImage.index? 'right' : 'left';
			}
		},

		created() {
			this.setOptions(this.options);
			this.setTransitions(this.transitions);
		},

		mounted() {
			if (this.images.length < 2 || this.transitionNames.length === 0)
				return;

			this.resize();

			this.preloadImages(this.images);

			window.addEventListener('resize', this.resize);
		},

		beforeDestroy() {
			window.removeEventListener('resize', this.resize);
		},

		methods: {
			preloadImages(images) {
				this.preload = images.slice(0);
			},

			addImage(i) {
				this.imagesLoaded++;

				let img = this.$refs.images[i];

				if (img.naturalWidth || img.width) {
					this.properties[i] = {
						src: img.src,
						width: img.naturalWidth || img.width,
						height: img.naturalHeight || img.height
					};
				}

				if (i === 0)
					this.$refs.image1.init();

				if (this.imagesLoaded === this.preload.length)
					this.init();
			},

			setOptions(options) {
				let currentSize = {
					width: this.config.width,
					height: this.config.height
				};

				this.config = Object.assign({}, this.config, options);

				this.size.width = this.config.width;
				this.size.height = this.config.height;

				if (currentSize.width !== this.config.width || currentSize.height !== this.config.height)
					this.resize();
			},

			setTransitions(transitions) {
				Object.assign(this.$options.components, transitions);

				this.transitionNames = Object.keys(transitions);

				if (this.transitionNames.length > 0)
					this.transition.last = this.transitionNames.length - 1;
			},

			resize() {
				this.size.width = undefined;
				this.size.height = undefined;

				if (this.config.width.indexOf('px') !== -1)
					this.size.width = parseInt(this.config.width);

				if (this.config.height.indexOf('px') !== -1)
					this.size.height = parseInt(this.config.height);

				if (this.size.width && this.size.height)
					return;

				this.$nextTick(() => {
					// Find width
					this.size.width = this.$refs.container.clientWidth;

					// Find height
					if (this.config.height === 'auto' && this.$refs.container.clientHeight) {
						this.size.height = this.$refs.container.clientHeight;

					} else {
						this.size.height = Math.floor(this.size.width / 16 * 9);
					}

					this.$refs.image1.init();
					this.$refs.image2.init();
				});
			},

			init() {
				this.properties = this.properties.filter((p) => p);
				this.preload = [];
				this.loaded = true;

				this.$refs.image2.init();

				this.$nextTick(() => {
					this.$refs.image1.reference = 'image1Index';
					this.$refs.image2.reference = 'image2Index';

					this.$refs.image1.setCss({ zIndex: 11 });
					this.$refs.image2.setCss({ zIndex: 10 });
				});

				if (this.config.autoplay === true)
					this.play();
			},

			toggleMouseOver(over) {
				if (this.touchable)
					return;

				this.mouseOver = over;
			},

			play(index) {
				this.config.autoplay = true;

				this.timer = setTimeout(() => {
					this.showImage(index);
				}, this.config.delay);
			},

			stop() {
				this.config.autoplay = false;

				clearTimeout(this.timer);
			},

			toggleAutoplay() {
				if (this.config.autoplay)
					this.stop();

				else
					this.play();
			},

			getIndex(index) {
				if (typeof index === 'number')
					return index;

				let currentIndex = this.currentImage.index;

				if (index === 'previous')
					return currentIndex > 0? currentIndex - 1 : this.images.length - 1;

				return currentIndex + 1 < this.images.length? currentIndex + 1 : 0;
			},

			showImage(index, transition) {
				if (!this.loaded || this.$refs.image1 === undefined)
					return;

				// If there is a transition running prevent showing new image
				if (this.transition.current !== undefined)
					return false;

				if (this.currentImage.index === index)
					return false;

				clearTimeout(this.timer);

				// Get image index if next or previous
				index = this.getIndex(index);

				// Prepare images
				let currentImage = this.currentImage;
				let nextImage = this.nextImage;

				this[nextImage.reference] = index;
				nextImage.show();

				this.$nextTick(() => {
					// Get transition
					if (transition === undefined)
						transition = this.nextTransition;

					if (transition) {
						this.transition.last = this.transitionNames.indexOf(transition);
						this.transition.current = transition;
					}

					this.$nextTick(() => {
						let timeout = transition !== undefined? this.$refs.transition.totalDuration + 20 : 0;

						setTimeout(() => {
							currentImage.setCss({ zIndex: 10 });
							nextImage.setCss({ zIndex: 11 });

							this.transition.current = undefined;

							// Play next if autoplay is true
							if (this.config.autoplay === true) {
								this.timer = setTimeout(() => {
									this.showImage('next');
								}, this.config.delay);
							}
						}, timeout);
					});
				});
			},

			dragging(event) {
				this.dragStartX = event.touches[0].clientX;
				this.dragStartY = event.touches[0].clientY;
				event.preventDefault();
			},

			releasing(event) {
				event.preventDefault();

				let triggerX = Math.floor(this.size.width / 3);
				let offsetX = event.changedTouches[0].clientX - this.dragStartX;

				if (offsetX > 0 && offsetX > triggerX) {
					this.showImage('previous');
					return;
				}

				if (offsetX < 0 && offsetX < -triggerX) {
					this.showImage('next');
					return;
				}

				if (this.index === undefined)
					return;

				let triggerY = Math.floor(this.size.height / 3);
				let offsetY = event.changedTouches[0].clientY - this.dragStartY;

				if (offsetY < 0 && offsetY < -triggerY) {
					this.index.show();
					return;
				}
			}
		}
	}
</script>

<style lang="scss">
	.vue-flux {
		position: relative;

		img {
			position: absolute;
			visibility: hidden;
		}
	}

	$spinner-size: 80px;

	.spinner {
		position: absolute;
		top: 50%;
		left: 50%;
		margin-top: -($spinner-size / 2);
		margin-left: -($spinner-size / 2);
		width: $spinner-size;
		height: $spinner-size;
		z-index: 12;

		.pct {
			position: absolute;
			right: 0;
			left: 0;
			height: $spinner-size;
			line-height: $spinner-size;
			text-align: center;
			font-weight: bold;
			z-index: 1;
		}

		.border {
			width: 100%;
			height: 100%;
			border: 14px solid #f3f3f3;
			border-top-color: #3498db;
			border-bottom-color: #3498db;
			border-radius: 50%;
			background-color: #f3f3f3;
			animation: spin 2s linear infinite;
		}
	}

	@keyframes spin {
		0% { transform: rotate(0deg); }
		100% { transform: rotate(360deg); }
	}

	.mask {
		position: relative;
		overflow: visible;
	}
</style>
